<template>
  <div class="controls">
    <button @click="checkSudoku()" class="icon">
      <span class="material-icons"> done </span>
    </button>
    <br />
    <button @click="resetGame()" class="icon">
      <span class="material-icons"> sync </span>
    </button>
    <br />
    <button @click="newGame()" class="icon">
      <span class="material-icons"> fiber_new </span>
    </button>
    <br />
    <button @click="importSudokuString()" class="icon">
      <span class="material-icons"> keyboard </span>
    </button>
    <br />
    <button
      @click="toggleSettings()"
      class="icon"
      :class="settingsShown ? 'selected' : ''"
    >
      <span class="material-icons"> settings </span>
    </button>
    <br />
    <button
      @click="toggleHelp()"
      class="icon"
      :class="helpShown ? 'selected' : ''"
    >
      <span class="material-icons"> help_outline </span>
    </button>
    <br />
    <Settings v-show="settingsShown" @close="settingsShown = false" />
    <Help v-show="helpShown" @close="helpShown = false" />
  </div>
</template>

<script>
import { parseSudokuString } from "@/helpers";
import { mapActions } from "vuex";

import Settings from "./Settings.vue";
import Help from "./Help.vue";

export default {
  name: "Controls",
  data: function () {
    return { settingsShown: false, helpShown: false };
  },
  components: { Settings, Help },
  methods: {
    ...mapActions(["resetBoard", "newBoard", "importBoard"]),

    checkSudoku: function () {
      if (parseSudokuString(this.$store.state.currentSudokuString)) {
        alert("all good");
      } else {
        alert("there`s a booboo");
      }
    },

    toggleSettings: function () {
      this.settingsShown = !this.settingsShown;
      this.helpShown = false;
    },
    toggleHelp: function () {
      this.helpShown = !this.helpShown;
      this.settingsShown = false;
    },

    resetGame: function () {
      if (confirm("Are you sure you want to reset the board?")) {
        this.resetBoard();
      }
    },

    newGame: function () {
      if (confirm("Are you sure you want to start a new game?")) {
        this.newBoard();
      }
    },

    importSudokuString: function () {
      let importString = prompt(
        "Start a new gamme by \n entering the sudoku string here"
      );
      if (importString === null) return; // pressed cancel

      let regex = new RegExp("[1-9.]{81}");
      if (!regex.test(importString)) {
        alert(
          "String must be 81 characters long, containing only 1-9 and '.'!"
        );
        return;
      }
      this.importBoard(importString);
    },
  },
};
</script>

<style>
.controls {
  margin-left: 20px;
  width: 150px;
  text-align: left;
  position: absolute;
  
  left: -webkit-calc(50% + 800px/2);
  left: -moz-calc(50% + 800px/2);
  left: calc(50% + 800px / 2);
  top: -webkit-calc(50% - 800px/2);
  top: -moz-calc(50% - 800px/2);
  top: calc(50% - 800px / 2);
}

button.icon {
  width: 60px;
  height: 60px;
  padding: 0;
}
button.icon span {
  color: #750b95;
  font-size: 40px;
  line-height: 55px;
}
button.icon.small {
  width: 30px;
  height: 30px;
}
button.icon.small span {
  font-size: 15px;
  line-height: 27px;
}
button.icon.selected span {
  color: #fff;
}
</style>