const highlight = {
    namespaced: true,
    state: () => ({
        selectedIndexes: null,
        selectedDigit: null,
        selectedDigitIndexes: [],
        selectedRow: null,
        selectedRowIndexes: [],
        selectedColumn: null,
        selectedColumnIndexes: [],
        selectedBox: null,
        selectedBoxIndexes: [],
    }),
    mutations: {
        setSelectedIndexes(state, index) {
            state.selectedIndexes = index.sort();
        },
        setSelectedDigitIndexes(state, index) {
            state.selectedDigitIndexes = index.sort();
        },
        setSelectedRow(state, row) {
            state.selectedRow = row;
        },
        setSelectedColumn(state, column) {
            state.selectedColumn = column;
        },
        setSelectedBox(state, box) {
            state.selectedBox = box;
        },
        setSelectedRowIndexes(state, rowIndexes) {
            state.selectedRowIndexes = rowIndexes;
        },
        setSelectedColumnIndexes(state, columnIndexes) {
            state.selectedColumnIndexes = columnIndexes;
        },
        setSelectedBoxIndexes(state, boxIndexes) {
            state.selectedBoxIndexes = boxIndexes;
        }
    },
    actions: {
        setSelectedIndexes(context, index) {

            const ROW_SIZE = context.rootState.ROW_SIZE
            const COL_SIZE = context.rootState.COL_SIZE
            const BOX_SIZE = context.rootState.BOX_SIZE
            // set index starting from 1
            context.commit('setSelectedIndexes', [index])

            // calculate and set row 
            let currentRow = Math.ceil(index / ROW_SIZE);
            context.commit('setSelectedRow', currentRow)

            // calculate and set row 
            let currentColumn = index % ROW_SIZE || ROW_SIZE;
            context.commit('setSelectedColumn', currentColumn)

            // calculate and set selected box 
            let boxesPerRow = ROW_SIZE / BOX_SIZE;
            let currentBoxRow = Math.ceil(currentRow / BOX_SIZE);
            let currentBoxCol = Math.ceil(currentColumn / BOX_SIZE);
            let currentBox = (currentBoxRow - 1) * boxesPerRow + currentBoxCol;
            context.commit('setSelectedBox', currentBox)

            let currentRowIndexes = [];
            let currentColumnIndexes = [];
            let currentBoxIndexes = [];

            let currentIndexRow, currentIndexColumn;// temp variables
            let curentIndexBoxRow, currentIndexBoxCol; // temp variables

            let currentSelectedDigit = parseInt(context.rootState.currentSudokuString.charAt(index - 1)).toString();
            let currentSelectedDigitindexes = []

            for (let i = 1; i <= ROW_SIZE * COL_SIZE; i++) {
                currentIndexRow = Math.ceil(i / ROW_SIZE);
                currentIndexColumn = i % ROW_SIZE || ROW_SIZE;

                if (context.rootState.currentSudokuString.charAt(i-1) == currentSelectedDigit) {
                    currentSelectedDigitindexes.push(i)
                }

                // handle row highlight
                if (Math.ceil(currentIndexRow) == currentRow) {
                    currentRowIndexes.push(i);
                }

                // handle column highlight
                if ((currentIndexColumn) == currentColumn) {
                    currentColumnIndexes.push(i);
                }

                // handle box highlight
                curentIndexBoxRow = Math.ceil(currentIndexRow / BOX_SIZE);
                currentIndexBoxCol = Math.ceil(currentIndexColumn / BOX_SIZE);

                if (curentIndexBoxRow == currentBoxRow && currentIndexBoxCol == currentBoxCol)
                    currentBoxIndexes.push(i);

                context.commit('setSelectedRowIndexes', currentRowIndexes)
                context.commit('setSelectedColumnIndexes', currentColumnIndexes)
                context.commit('setSelectedBoxIndexes', currentBoxIndexes)
                context.commit('setSelectedDigitIndexes', currentSelectedDigitindexes)

            }
        },
        updateIndexes: function (context, index) {
            let newIndexes = context.state.selectedIndexes || [];
            if (!newIndexes.includes(index)) newIndexes.push(index);
            else newIndexes = newIndexes.filter((el) => el != index);
            context.commit('setSelectedIndexes', newIndexes)
        },
        addIndex: function (context, index) {
            let newIndexes = context.state.selectedIndexes || [];
            if (!newIndexes.includes(index)) newIndexes.push(index);
            context.commit('setSelectedIndexes', newIndexes)
        },
        moveLeft: function (context) {
            // default is 2 since we'll decrease it
            let index = 2
            if (context.state.selectedIndexes && context.state.selectedIndexes[0]) { index = context.state.selectedIndexes[0] }

            // if we are on the first element on a row - move to the end
            if (index % context.rootState.ROW_SIZE == 1) {
                index += context.rootState.ROW_SIZE
            }

            // decrease the index
            index--;
            context.commit('setSelectedIndexes', [index]);
        },
        moveRight: function (context) {
            let index = 1
            if (context.state.selectedIndexes && context.state.selectedIndexes[0]) { index = context.state.selectedIndexes[0] }

            // if we are on the last element on a row - move to the beggining
            if (index % context.rootState.ROW_SIZE == 0) {
                index -= context.rootState.ROW_SIZE
            }

            // decrease the index
            index++;
            context.commit('setSelectedIndexes', [index]);
        },
        moveUp: function (context) {
            let index = 1
            if (context.state.selectedIndexes && context.state.selectedIndexes[0]) { index = context.state.selectedIndexes[0] }
            // increase the column
            index -= context.rootState.ROW_SIZE;

            // if we are on the last element on a column - move to the top
            let row = (Math.ceil(index / context.rootState.COL_SIZE));
            if (row < 1) {
                index = index + context.rootState.ROW_SIZE * context.rootState.COL_SIZE;
            }
            context.commit('setSelectedIndexes', [index]);
        },
        moveDown: function (context) {
            let index = 1
            if (context.state.selectedIndexes && context.state.selectedIndexes[0]) { index = context.state.selectedIndexes[0] }
            // increase the column
            index += context.rootState.ROW_SIZE;

            // if we are on the last element on a column - move to the top
            let row = (Math.ceil(index / context.rootState.COL_SIZE));
            if (row > context.rootState.COL_SIZE) {
                index = index % context.rootState.ROW_SIZE;
            }
            context.commit('setSelectedIndexes', [index]);
        }

    },
    getters: {

        selectedIndexes: state => state.selectedIndexes,
        selectedDigit: state => state.selectedDigit,
        selectedRow: state => state.selectedRow,
        selectedColumn: state => state.selectedColumn,
        selectedBox: state => state.selectedBox,
        selectedDigitIndexes: state => state.selectedDigitIndexes,
        selectedRowIndexes: state => state.selectedRowIndexes,
        selectedColumnIndexes: state => state.selectedColumnIndexes,
        selectedBoxIndexes: state => state.selectedBoxIndexes,

    }
}

export default highlight