<template>
  <li
    :class="classValue"
    @mousedown="handleClick(index, $event)"
    @mouseover="handleDrag(index, $event)"
  >
    {{ value == "." ? "" : value }}
    <CornerClues v-show="value == '.'" ref="CornerClues" />
    <CenterClues v-show="value == '.'" ref="CenterClues" />
  </li>
</template>

<script>
import { mapActions, mapGetters } from "vuex";

import CornerClues from "./CornerClues.vue";
import CenterClues from "./CenterClues.vue";

export default {
  name: "Cell",
  components: { CornerClues, CenterClues },
  props: {
    index: Number,
    value: String,
    initialValue: String,
  },
  methods: {
    ...mapActions("highlight", [
      "setSelectedIndexes",
      "updateIndexes",
      "addIndex",
    ]),
    ...mapActions(["setMousedown"]),
    handleClick: function (index, evt) {
      this.setMousedown(true);
      if (evt.ctrlKey) {
        this.updateIndexes(index);
      } else {
        this.setSelectedIndexes(index);
      }
    },
    handleDrag: function (index) {
      if (this.mousedown) this.addIndex(index);
    },
    editClue: function (clue) {
      if (this.inputMethod == "corner") this.$refs.CornerClues.editClue(clue);
      if (this.inputMethod == "center") this.$refs.CenterClues.editClue(clue);
    },
    clearClues: function () {
      if (this.inputMethod == "corner") this.$refs.CornerClues.clearClues();
      if (this.inputMethod == "center") this.$refs.CenterClues.clearClues();
    },
  },
  computed: {
    ...mapGetters("highlight", [
      "selectedIndexes",
      "selectedDigit",
      "selectedDigitIndexes",
      "selectedRowIndexes",
      "selectedColumnIndexes",
      "selectedBoxIndexes",
    ]),
    ...mapGetters(["inputMethod", "mousedown"]),
    ...mapGetters("settings", ["hlRCB", "hlDigits"]),
    classValue: function () {
      let className = "cell ";

      // handle selected cell
      if (this.selectedIndexes && this.selectedIndexes.includes(this.index))
        className += "selected ";

      // same digits highlight
      if (
        this.hlDigits &&
        this.selectedDigitIndexes &&
        this.selectedDigitIndexes.includes(this.index)
      )
        className += "sameDigit ";

      // handle selected row
      if (this.hlRCB && this.selectedRowIndexes.includes(this.index))
        className += "sameRow ";

      if (this.hlRCB && this.selectedColumnIndexes.includes(this.index))
        className += "sameColumn ";

      if (this.hlRCB && this.selectedBoxIndexes.includes(this.index))
        className += "sameBox ";

      if (this.initialValue == ".") className += "customDigit ";

      return className;
    },
  },
};
</script>
<style lang="scss" scoped>
.cell {
  position: relative;
  color: #374d54;
  font-smooth: 2em;
}
</style>