import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

import highlight from './highlight.js'
import settings from './settings.js'
import { setCharAt, sudoku } from '@/helpers'
import { constants } from '@/constants'


let state = {
	ROW_SIZE: 9,
	COL_SIZE: 9,
	BOX_SIZE: 3,
	inputMethod: constants.INPUT_METHODS.DEFAULT,
	mousedown: false,

	/*
	.17..69..356194.2..89..71.6.65...273872563419.43...685521......798..53..634...59.
	.1..8..7.8...3...9...9.1.....3...6..94.....53..8...9.....5.6...7...1...2.5..2..1.
	6..1..........2..3..7.9...2....5...4.2..3..1....7...8.5..8...7....6..4....4.....8
	*/

	initialSudokuString: '',
	currentSudokuString: '',

}
let mutations = {
	initialiseStore(state) {
		// Check if the ID exists
		let storedState = JSON.parse(localStorage.getItem('sudokustore'));
		if (storedState && storedState.initialSudokuString) {
			// Replace the state object with the stored item
			this.replaceState(
				Object.assign(state, storedState)
			);
		} else {
			let generator = {}
			sudoku(generator)
			let difficulty = {
				"easy": 62,
				"medium": 53,
				"hard": 44,
				"very-hard": 35,
				"insane": 26,
				"inhuman": 17,
			}
			let str = generator.sudoku.generate(difficulty['hard'])
			state.initialSudokuString = str
			state.currentSudokuString = str
		}
	},
	setCurrentSudokuString(state, string) {
		state.currentSudokuString = string;
	},
	setInitialSudokuString(state, string) {
		state.initialSudokuString = string;
	},
	setInputMethod(state, inputMethod) {
		state.inputMethod = inputMethod;
	},
	setMousedown(state, mousedown) {
		state.mousedown = mousedown;
	}
}

let actions = {
	setDigit: function (context, digit) {
		let curentString = context.state.currentSudokuString;
		context.rootState.highlight.selectedIndexes.forEach(index => {
			// we should not change the value of the initial clues
			if (context.state.initialSudokuString.charAt(index - 1) == ".") {
				curentString = setCharAt(curentString, index - 1, digit)
			}
		});
		context.commit('setCurrentSudokuString', curentString)
	},
	clearCell: function (context) {
		actions.setDigit(context, '.');
	},
	setInputMethod: function (context, inputMethod) {
		context.commit('setInputMethod', inputMethod)
	},
	resetBoard: function (context) {
		context.commit('setCurrentSudokuString', context.state.initialSudokuString)
	},
	newBoard: function (context) {
		actions.importBoard(context, '');
	},

	importBoard: function (context, string) {
		context.commit('setInitialSudokuString', string)
		context.commit('setCurrentSudokuString', string)
		context.commit('initialiseStore', string)
	},

	toggleInputMethod: function (context) {

		let prev = ''
		let next = '';
		Object.keys(constants.INPUT_METHODS).forEach((key) => {

			if (key == 'DEFAULT') return;
			let value = constants.INPUT_METHODS[key]
			if (prev == context.state.inputMethod)
				next = value;
			prev = value;
		})

		if (next == '') {
			next = Object.values(constants.INPUT_METHODS)[0];
		}

		context.commit('setInputMethod', next);

	},
	setMousedown: function (context, mousedown) {
		context.commit('setMousedown', mousedown)
	}
}


const store = new Vuex.Store({
	state,
	mutations,
	actions,
	modules: {
		highlight,
		settings
	},
	getters: {
		ROW_SIZE: state => state.ROW_SIZE,
		COL_SIZE: state => state.COL_SIZE,
		currentSudokuString: state => state.currentSudokuString,
		initialSudokuString: state => state.initialSudokuString,
		inputMethod: state => state.inputMethod,
		mousedown: state => state.mousedown,
	}
})

store.subscribe((_mutation, state) => {
	// Store the state object as a JSON string
	localStorage.setItem('sudokustore', JSON.stringify(state));
});

export { store }