<template>
  <div id="app">
    <h1>Sudoku</h1>
    <Grid ref="Grid" />
    <div class="input-methods">
      <button
        :class="inputMethod == constants.INPUT_METHODS.CORNER ? 'selected' : ''"
        @click="setInputMethod(constants.INPUT_METHODS.CORNER)"
      >
        Corner
      </button>
      <br />
      <button
        :class="inputMethod == constants.INPUT_METHODS.CENTER ? 'selected' : ''"
        @click="setInputMethod(constants.INPUT_METHODS.CENTER)"
      >
        Center
      </button>
      <br />
      <button
        :class="inputMethod == constants.INPUT_METHODS.VALUE ? 'selected' : ''"
        @click="setInputMethod(constants.INPUT_METHODS.VALUE)"
      >
        Value
      </button>
    </div>
    <Controls />
  </div>
</template>

<script>
import { constants } from "@/constants";

import { mapActions, mapGetters } from "vuex";
import Grid from "./components/Grid.vue";
import Controls from "./components/UI/Controls.vue";

export default {
  name: "App",
  components: {
    Grid,
    Controls,
  },
  created() {
    this.constants = constants;
  },
  methods: {
    ...mapActions([
      "setDigit",
      "clearCell",
      "setInputMethod",
      "toggleInputMethod",
      "setMousedown",
    ]),
    ...mapActions("highlight", ["moveLeft", "moveRight", "moveUp", "moveDown"]),
    handleKeyPress: function (evt) {
      switch (evt.keyCode) {
        case 37: //ArrowLeft
          this.moveLeft();
          evt.preventDefault();
          break;

        case 39: //ArrowRight
          this.moveRight();
          evt.preventDefault();
          break;

        case 38: //ArrowUp
          this.moveUp();
          evt.preventDefault();
          break;

        case 40: //ArrowDown
          this.moveDown();
          evt.preventDefault();
          break;

        case 32: // spacebar
          this.toggleInputMethod();
          evt.preventDefault();
          break;

        case 8: // Backspace
        case 46: // Delete
          if (this.inputMethod == constants.INPUT_METHODS.VALUE) {
            this.clearCell();
          } else {
            this.$refs.Grid.clearClues();
          }
          evt.preventDefault();
          break;
      }

      // digits
      if (
        this.inputMethod == constants.INPUT_METHODS.VALUE &&
        !isNaN(parseInt(evt.key))
      ) {
        this.setDigit(evt.key);
        return;
      }
      // more digits but for clues
      if (!isNaN(parseInt(evt.key))) {
        this.$refs.Grid.editClue(evt.key);
        return;
      }
    },

    handleMouseUp: function () {
      this.setMousedown(false);
    },
  },
  computed: {
    ...mapGetters(["inputMethod"]),
  },
  mounted() {
    let that = this;
    document.addEventListener("keydown", function (evt) {
      that.handleKeyPress(evt);
    });
    document.addEventListener("mouseup", function () {
      that.handleMouseUp();
    });
  },
};
</script>

