<template>
  <div class="centerclues">
    <div class="holder">
      <!-- <span v-for="clue in clues" :key="clue">{{ clue }}</span> -->
      {{ val }}
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  name: "CenterClues",
  data: function () {
    return {
      clues: [],
    };
  },
  computed: {
    val: function () {
      return this.clues.join("");
    },
  },
  methods: {
    editClue: function (clue) {
      if (!this.clues.includes(clue)) this.clues.push(clue);
      else this.clues = this.clues.filter((el) => el != clue);
      this.clues = this.clues.sort();
    },
    clearClues: function () {
      this.clues = [];
    },
  },
};
</script>
<style lang="scss" scoped>
$fontSize: 20px;
.centerclues {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.holder {
  position: absolute;
  left: 0;
  top: calc(50% - #{$fontSize/2});
  width: 100%;
  height: $fontSize;
  font-size: $fontSize;
  line-height: $fontSize;
  color: blue;
}
</style>
