<template>
  <div class="cornerlclues">
    <span v-for="clue in clues" :key="clue">{{ clue }}</span>
  </div>
</template>

<script>
export default {
  name: "CornerClues",
  data: function () {
    return {
      clues: [],
    };
  },
  methods: {
    editClue: function (clue) {
      if (!this.clues.includes(clue)) this.clues.push(clue);
      else this.clues = this.clues.filter((el) => el != clue);
      this.clues = this.clues.sort();
    },
    clearClues: function () {
      this.clues = [];
    },
  },
};
</script>
<style lang="scss" scoped>
.cornerlclues {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}
.cornerlclues span {
  position: absolute;
  font-size: 19px;
  color: blue;
  display: block;
  float: left;
  height: 33%;
  width: 33%;
}
.cornerlclues span:nth-child(1) {
  top: 0;
  left: 0;
}
.cornerlclues span:nth-child(2) {
  top: 0;
  right: 0;
}
.cornerlclues span:nth-child(3) {
  bottom: 0;
  left: 0;
}
.cornerlclues span:nth-child(4) {
  bottom: 0;
  right: 0;
}
.cornerlclues span:nth-child(5) {
  top: 0;
  left: 33%;
}
.cornerlclues span:nth-child(6) {
  bottom: 0;
  right: 33%;
}
.cornerlclues span:nth-child(7) {
  top: 33%;
  left: 0;
}
.cornerlclues span:nth-child(8) {
  top: 33%;
  right: 0;
}
.cornerlclues span:nth-child(9) {
  top: 33%;
  left: 33%;
}
</style>
