let mutations = {
    setHlRCB(state, hlRCB) {
        state.hlRCB = hlRCB;
    },
    setHlDigits(state, hlDigits) {
        state.hlDigits = hlDigits;
    },
}

let actions = {
    setHlRCB(context, hlRCB) {
        context.commit('setHlRCB', hlRCB)
    },

    setHlDigits(context, hlDigits) {
        context.commit('setHlDigits', hlDigits)
    },
}

let getters = {
    hlRCB: state => state.hlRCB,
    hlDigits: state => state.hlDigits,
}

const settings = {
    namespaced: true,
    state: {
        hlRCB: true,
        hlDigits: true
    },
    mutations,
    actions,
    getters
}

export default settings