function parseSudokuString(sudokuSring) {
    var rows = [];
    var cols = [];
    var cells = [];

    let column, row, cell, value

    for (let index = 0; index < sudokuSring.length; index++) {

        column = (index + 9) % 9 // returns the idex of the colum starting from 0
        row = Math.floor(index / 9) // returns the idex of the row starting from 0
        cell = Math.floor(index / 3) % 3 + 3 * Math.floor(index / 27) // cell index starting from 0

        rows[row] = rows[row] || []; // initiate the array if needed
        cols[column] = cols[column] || []; // initiate the array if needed
        cells[cell] = cells[cell] || []; // initiate the array if needed

        value = sudokuSring.charAt(index);
        rows[row][column] = value // store the value
        cols[column][row] = value; // store the value
        cells[cell].push(value);
    }

    var valid = true;
    function checkArray(arr) {

        if (arr.sort().join("") != "123456789") {
            valid = false;
        }
    }

    rows.forEach(checkArray);
    cols.forEach(checkArray);
    cells.forEach(checkArray);
    return valid
}

export { parseSudokuString }